body {
  color: #2a2a2a;
  font-family: Helvetica Neue, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, sans-serif;
  line-height: 1.5;
}

.container {
  width: 1200px;
  margin: 0 auto;
  padding: 50px;
}

.nav__list {
  margin-bottom: 20px;
  font-size: 25px;
}

.nav__link:hover {
  color: #00f;
}

.home__link {
  font-size: 35px;
}

input {
  border: 1px solid #aaa;
  border-radius: 3px;
}

input:focus {
  border: 1px solid #aaa;
  outline: none;
}

#newGame {
  width: 140px;
  height: 27px;
  color: #fff;
  background-color: #2196f3;
  border-radius: 4px;
  margin: 5px auto 0;
  transition: all .4s ease-in-out;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

#newGame:hover {
  color: #000;
  transform: scale(1.01);
}

*, :after, :before {
  box-sizing: border-box;
  border: none;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.box {
  width: 100px;
  height: 100px;
  background-color: #0ff;
  position: absolute;
}

.box1 {
  top: 20px;
  left: 20px;
}

.box2 {
  top: 20px;
  left: 300px;
}

.box3 {
  top: 400px;
  left: 600px;
}

.box4 {
  top: 600px;
  left: 300px;
}



